*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

img {
  max-width: 100%;
  height: auto;
  font-size: 0;
  display: block;
}

picture {
  font-size: 0;
  display: block;
  overflow: hidden;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  padding: 0;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  color: var(--main-text-color-dark-theme);
  letter-spacing: -.04em;
  stroke: var(--black-color);
  background-color: var(--background-color-light-theme);
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28571;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
    display: flex;
  }
}

.section {
  padding-top: 112px;
  padding-bottom: 80px;
}

@media screen and (min-width: 768px) {
  .section {
    padding-bottom: 172px;
  }
}

@media screen and (min-width: 1440px) {
  .section {
    padding-top: 112px;
    padding-bottom: 72px;
  }
}

.section-title {
  letter-spacing: -.02em;
  font-size: 40px;
  font-weight: 500;
  line-height: 1;
}

@media screen and (min-width: 768px) {
  .section-title {
    font-size: 68px;
  }
}

@font-face {
  font-family: DM Sans;
  font-style: italic;
  font-weight: 400;
  font-display: swap;
  src: url("https://fonts.gstatic.com/s/dmsans/v11/rP2Fp2ywxg089UriCZaIGA.ttf") format("truetype");
}

@font-face {
  font-family: DM Sans;
  font-style: italic;
  font-weight: 500;
  font-display: swap;
  src: url("https://fonts.gstatic.com/s/dmsans/v11/rP2Ap2ywxg089UriCZaw7BymDg.ttf") format("truetype");
}

@font-face {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("https://fonts.gstatic.com/s/dmsans/v11/rP2Hp2ywxg089UriOZQ.ttf") format("truetype");
}

@font-face {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("https://fonts.gstatic.com/s/dmsans/v11/rP2Cp2ywxg089UriAWCrOB8.ttf") format("truetype");
}

@font-face {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("https://fonts.gstatic.com/s/dmsans/v11/rP2Cp2ywxg089UriASitOB8.ttf") format("truetype");
}

:root {
  --main-text-color-dark-theme: #202024;
  --main-text-color-light-theme: #f6f6f6;
  --secondary-text-color: #b4afaf;
  --brand-color: #4f2ee8;
  --white-color: #fff;
  --black-color: #111;
  --accent-color: #eac645;
  --background-color-light-theme: #f6f6f6;
  --background-color-dark-theme: #202024;
  --categories-list-light-theme: #1119;
  --categories-list-dark-theme: #fff;
  --brand-color: #4f2ee8;
  --white-cl: #fff;
  --black-cl: #111;
  --yellow-cl: #ff0;
  --hover-light-theme: #4f2ee8;
  --hover-dark-theme: #eac645;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

.catygories-and-support {
  padding-bottom: 40px;
}

.categories-list {
  height: 228px;
  width: 335px;
  flex-direction: column;
  gap: 14px;
  margin: 0 0 40px;
  padding-right: 28px;
  display: inline-flex;
  overflow-y: scroll;
}

.categories-list::-webkit-scrollbar {
  width: 8px;
  border-radius: 12px;
}

.categories-list::-webkit-scrollbar-thumb {
  height: 114px;
  background: #e0e0e0;
  border-radius: 12px;
}

.side-category {
  text-transform: capitalize;
  letter-spacing: -.02em;
  transition: color var(--transition);
  padding: 5px 0;
  font-size: 16px;
  line-height: 18px;
}

.categories-list li:first-child a {
  padding: 0 0 5px;
}

.selected-category a, .side-category:hover, .side-category:active {
  text-transform: uppercase;
  color: var(--brand-color);
  font-size: 14px;
  font-weight: 700;
}

.book_temp {
  width: 335px;
  height: 485px;
  object-fit: cover;
  cursor: pointer;
  border-radius: 8px;
}

.category_title_last_word {
  letter-spacing: -.04em;
  color: var(--brand-color);
  font-size: 32px;
  font-weight: 700;
  line-height: 38px;
}

.outlineli {
  width: 335px;
}

.title_categories {
  text-transform: capitalize;
  letter-spacing: -.04em;
  font-size: 32px;
  line-height: 38px;
}

.js_books_view {
  flex-wrap: wrap;
  justify-content: center;
  row-gap: 40px;
  padding-top: 40px;
  display: flex;
}

.adition-category-title {
  letter-spacing: .03em;
  text-transform: uppercase;
  margin-bottom: 18px;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}

.book-info {
  padding-top: 14px;
}

.book-info-title {
  letter-spacing: -.04em;
  text-transform: uppercase;
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.book-info-autor {
  text-transform: none;
  letter-spacing: -.04em;
  color: var(--secondary-text-color);
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 14px;
}

.load-more-books {
  border: 2px solid var(--brand-color);
  letter-spacing: -.01em;
  text-transform: uppercase;
  color: inherit;
  transition: color var(--transition), background var(--transition);
  background: none;
  border-radius: 40px;
  flex-direction: row;
  align-items: flex-start;
  margin: 8px 0 0 auto;
  padding: 14px 28px;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  display: flex;
}

.load-more-books:hover, .load-more-books:focus {
  color: var(--white-color);
  background: var(--brand-color);
}

.list-books .hidde:nth-child(n+2) {
  display: none;
}

.book-tumb {
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.book-tumb-text {
  background: var(--accent-color);
  width: 100%;
  height: 70px;
  transition: transform var(--transition);
  letter-spacing: -.04em;
  text-transform: uppercase;
  color: var(--white-color);
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  display: flex;
  position: absolute;
  bottom: 0;
  transform: translateY(70px);
}

.outlineli:hover .book-tumb-text, .outlineli:focus .book-tumb-text {
  transform: translateY(0);
}

.no-btn {
  display: none;
}

.list-books {
  flex-wrap: wrap;
  gap: 40px;
  display: flex;
}

.book-loader {
  --color: var(--loader);
  --duration: 6.8s;
  width: 32px;
  height: 12px;
  zoom: 1.5;
  margin: 70px auto;
  position: relative;
}

@media (min-width: 768px) {
  .book-loader {
    margin: 40px auto;
  }

  .catygories-and-support {
    gap: 32px;
    padding-bottom: 100px;
    display: flex;
  }

  .categories-list {
    height: 472px;
    width: 337px;
    gap: 22px;
    margin-bottom: 0;
  }

  .list-books {
    gap: 40px 25px;
  }

  .list-books .hidde:nth-child(-n+3) {
    display: block;
  }

  .book_temp {
    width: 218px;
    height: 316px;
  }

  .outlineli {
    width: 218px;
  }

  .js_books_view {
    gap: 40px 25px;
  }

  .category-books {
    padding-bottom: 0;
  }

  .load-more-books {
    margin: 20px 0 0 auto;
  }
}

@media (min-width: 1440px) {
  .catygories-support-books {
    display: flex;
  }

  .catygories-and-support {
    flex-direction: column;
    gap: 0;
    display: inline-flex;
  }

  .categories-list {
    height: 472px;
    width: 356px;
    gap: 22px;
    margin-bottom: 86px;
    display: inline-flex;
  }

  .list-books .hidde:nth-child(n) {
    display: block;
  }

  .book_temp {
    width: 180px;
    height: 256px;
  }

  .outlineli {
    width: 180px;
  }

  .list-books {
    gap: 24px;
    display: flex;
  }

  .wrapper-categories-box {
    max-width: 990px;
    flex-direction: column;
    margin-left: 40px;
    display: inline-flex;
  }

  .js_books_view {
    width: 990px;
    gap: 40px 24px;
  }

  .js_books_view .outlineli, .list-books li {
    flex-basis: calc(20% - 19.2px);
  }

  .category-books {
    padding-bottom: 0;
  }

  .load-more-books {
    margin: 32px 0 0 auto;
  }

  .list-outlineli {
    flex-wrap: wrap;
    display: flex;
  }

  .wrap {
    min-height: 100vh;
    margin: 0 auto;
    padding: 50px;
  }
}

.container-header {
  width: 100%;
  z-index: 21;
  width: 100%;
  max-width: 1440px;
  height: 64px;
  background-color: #f6f6f6;
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  position: relative;
  top: 0;
}

.header-nav-menu {
  align-items: center;
  padding-left: 130px;
  display: flex;
}

.toggle-menu {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0 0 0 auto;
  display: flex;
}

.switch-menu {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.switch-menu input {
  opacity: 0;
}

.slider-menu {
  cursor: pointer;
  background: linear-gradient(#dcdcdc 0%, #4f2ee8 100%);
  transition: all .4s;
  position: absolute;
  inset: 0;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.slider-menu:before {
  content: "";
  height: 20px;
  width: 20px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 18px;
  box-shadow: 1px 1px 2px #0000001a, 0 2px 2px #0000001a, 1px 2px 4px #0000001a;
}

input:checked + .slider-menu {
  background: linear-gradient(#686868 0%, #4f2ee8 100%);
  box-shadow: inset 1px 1px 2px #0000001a;
}

input:checked + .slider-menu:before {
  transform: translateX(-16px);
}

.slider-menu, .circle-menu {
  border-radius: 24px;
}

.circle-menu:before {
  border-radius: 50%;
}

input:checked + .circle-menu:before {
  background-color: #fff;
}

.mobile-burger-menu {
  z-index: 20;
  width: 100vw;
  height: 100vh;
  background-image: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.mobile-menu-container {
  height: 85vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.mobile-menu-list {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 16px;
  display: inline-flex;
}

.mobile-menu-item {
  letter-spacing: -.56px;
  border-radius: 36px;
  align-items: center;
  display: flex;
}

.mobile-menu-link {
  letter-spacing: -.02em;
  color: #fff;
  align-items: center;
  padding: 8px 16px;
  display: flex;
}

.mobile-menu-link:hover, .mobile-menu-link:focus {
  color: #000;
  background-color: #eac645;
  border: 1.5px solid #0000;
  border-radius: 36px;
  font-weight: 700;
  transition: border-color .3s, background-color .3s, font-weight .3s;
}

.activeItem {
  color: #000;
  background-color: #eac645;
  border: 1.5px solid #0000;
  border-radius: 36px;
  margin-left: 15px;
  font-weight: 700;
}

.menu-close {
  stroke: inherit;
  background-color: #0000;
  border: none;
  padding: 0;
  line-height: 0;
}

.mobile-menu-icon {
  fill: #fff;
  stroke: none;
  margin-left: 4px;
}

.mobile-menu-img {
  width: 100%;
  z-index: -1;
  position: absolute;
  top: 55%;
  left: 0%;
}

.mobile-menu-log-in {
  margin: 0 auto;
}

.log-btn {
  max-width: 118px;
  width: 100%;
  height: 48px;
  color: #111;
  letter-spacing: -.56px;
  background: #fff;
  border: 1.5px solid #111;
  border-radius: 18px;
  justify-content: center;
  align-items: center;
  gap: 4px;
  margin-left: 20px;
  padding: 13px 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  display: flex;
  position: absolute;
  top: 90%;
}

.header {
  z-index: 4;
  width: 100%;
  background-color: inherit;
  position: fixed;
  top: 0;
}

.header-container {
  width: 100%;
  height: 64px;
  max-width: 375px;
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 18px 20px;
  display: flex;
}

.header-logo {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header-logo-icon {
  stroke: none;
}

.header-logo-p {
  padding: 5px;
  font-size: 18px;
  font-weight: 600;
  line-height: 18px;
}

.header-nav {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  display: flex;
  position: relative;
}

.header-nav-list {
  align-items: center;
  gap: 8px;
  display: flex;
}

.header-nav-items a {
  color: inherit;
  letter-spacing: -.04em;
  border-radius: 36px;
  align-items: center;
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28571;
  display: flex;
}

.header-nav-item .header-nav-items:not(.disabled-link) a:hover, .header-nav-item .header-nav-items:not(.disabled-link) a:focus, .header-nav-item .header-nav-items:not(.disabled-link) a.active {
  background-color: #eac645;
  border: 1.5px solid #0000;
  border-radius: 36px;
  font-weight: 700;
  transition: border-color .3s, background-color .3s, font-weight .3s;
}

.header-nav-link-icon {
  stroke: #0000;
  margin-left: 8px;
}

.disabled-link {
  pointer-events: none;
  cursor: default;
  opacity: .4;
  text-decoration: none;
}

.header-burger-menu-button {
  background-color: #0000;
  border: none;
  line-height: 0;
}

.header-burger-menu {
  stroke: inherit;
  fill: inherit;
  color: inherit;
  cursor: pointer;
  height: 28px;
  background: none;
  border: none;
  padding: 0;
}

.header-theme-switch {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0 0 0 auto;
  display: flex;
}

.switch {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
}

.slider {
  cursor: pointer;
  background: linear-gradient(#dcdcdc 0%, #4f2ee8 100%);
  transition: all .4s;
  position: absolute;
  inset: 0;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.slider:before {
  content: "";
  height: 20px;
  width: 20px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 18px;
  box-shadow: 1px 1px 2px #0000001a, 0 2px 2px #0000001a, 1px 2px 4px #0000001a;
}

input:checked + .slider {
  background: linear-gradient(#686868 0%, #4f2ee8 100%);
  box-shadow: inset 1px 1px 2px #0000001a;
}

input:checked + .slider:before {
  transform: translateX(-16px);
}

.slider, .circle {
  border-radius: 24px;
}

.circle:before {
  border-radius: 50%;
}

input:checked + .circle:before {
  background-color: #fff;
}

.arrow, .arrow-right {
  stroke: #eac645;
}

.header-sign-up {
  width: 184px;
  height: 48px;
  color: #fff;
  stroke: #eac645;
  background: #4f2ee8;
  border: 1.5px solid #111;
  border-radius: 18px;
  justify-content: space-between;
  align-items: center;
  padding: 12px 14px;
  line-height: 18px;
  text-decoration: none;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.header-sign-up:hover, .header-sign-up:focus {
  color: #eac645;
  border-color: #eac645;
}

.header-sign {
  margin-left: 24px;
  display: inline-flex;
  position: relative;
}

.modal {
  width: 100%;
  height: 100%;
  z-index: 999;
  background-color: #4f2ee8;
  padding: 24px;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  background-color: #fff;
  border: 2px solid #111;
  border-radius: 18px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%)scale(1);
}

.modal-header {
  justify-content: right;
  padding: 16px;
  display: flex;
}

.modal-title {
  margin: 0;
}

.modal-close {
  cursor: pointer;
  color: #999;
  background: none;
  border: none;
  padding: 24px 24px 28px 0;
  font-size: 24px;
}

.form-switch {
  text-align: center;
  margin-top: 20px;
  margin-bottom: 40px;
  font-size: 18px;
}

#switch-signin, #switch-signup {
  color: #00f;
  cursor: pointer;
}

#sign-up-form {
  display: none;
}

.modal.open {
  background-color: #bbb;
  display: flex;
}

.sign-form {
  padding: 0 40px 20px;
}

.intput-sign {
  width: 100%;
  height: 64px;
  border: 2px solid #111;
  border-radius: 40px;
  margin-bottom: 28px;
  padding: 16px 0 16px 24px;
  display: block;
  overflow: hidden;
}

.svg-icon {
  fill: #fff;
  margin: 18px 32px 18px 0;
  position: absolute;
  right: 0;
  transform: translateY(-50%);
}

#switch-signup.active, #switch-signin.active {
