.loader-title {
  color: inherit;
  text-align: center;
  text-transform: uppercase;
  font-family: sans-serif;
  font-size: 20px;
  position: relative;
}

.loader-title:after {
  content: "";
  animation: Dots .6s cubic-bezier(0, .39, 1, .68) infinite;
  position: absolute;
}

.loader {
  margin: 5% auto 30px;
}

.book {
  width: 60px;
  height: 45px;
  perspective: 150px;
  border: 4px solid navy;
  position: relative;
}

.page {
  width: 30px;
  height: 45px;
  transform-style: preserve-3d;
  transform-origin: 0;
  background: #00008b;
  border: 4px solid navy;
  border-left: 1px solid #00008b;
  margin: 0;
  display: block;
  position: absolute;
  top: -4px;
  right: -4px;
  overflow: hidden;
}

.book .page:nth-child(1) {
  animation: pageTurn .25s cubic-bezier(0, .39, 1, .68) .8s infinite;
}

.book .page:nth-child(2) {
  animation: pageTurn .25s cubic-bezier(0, .39, 1, .68) .725s infinite;
}

.book .page:nth-child(3) {
  animation: pageTurn .25s cubic-bezier(0, .39, 1, .68) .25s infinite;
}

@-webkit-keyframes pageTurn {
  0% {
    transform: rotateY(0deg);
  }

  10% {
    background: #00bfff;
  }

  40% {
    background: #00008b;
    transform: rotateY(-180deg);
  }

  100% {
    background: #00008b;
    transform: rotateY(-180deg);
  }
}

@keyframes pageTurn {
  0% {
    transform: rotateY(0deg);
  }

  20% {
    background: #00bfff;
  }

  40% {
    background: #00008b;
    transform: rotateY(-180deg);
  }

  100% {
    background: #00008b;
    transform: rotateY(-180deg);
  }
}

@keyframes Dots {
  0% {
    content: "";
  }

  33% {
    content: ".";
  }

  66% {
    content: "..";
  }

  100% {
    content: "...";
  }
}

